body{
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4;
}

.loading{
  width: 100%;
  min-height: 50vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.loading__img{
  max-width: 250px;
  height: auto;
}

.header {
  background-color: #512689;

  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;

  max-width: 1440px;
  margin: 0 auto;
  padding: 32px 80px;
  box-sizing: border-box;
}

.header__button {
  padding: 8px 18px;
  margin: 0;
  box-sizing: border-box;
  background: transparent;
  border-radius: 8px;
  border: 1px solid white;
  color: white;
  cursor: pointer;

  width: -webkit-min-content;

  width: min-content;

  transition: -webkit-transform 300ms ease-in;

  transition: transform 300ms ease-in;

  transition: transform 300ms ease-in, -webkit-transform 300ms ease-in;
}

.header__button_right {
  align-self: flex-end;
}

.header__button:hover {
  opacity: 0.7;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.header__button-text {
  font-size: 16px;
}

.header__button-img {
  display: none;
  max-width: 18px;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: center;
}

.header__title {
  color: white;
  font-size: 64px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header__subtitle {
  font-size: 20px;
  text-align: center;

  padding: 0;
  margin: 0;
  box-sizing: border-box;

  color: white;

  max-width: 60%;
}

.header__subtitle_profile {
  text-align: left;
  max-width: 100%;
}

.header__wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: -webkit-min-content -webkit-min-content auto -webkit-min-content;
  grid-template-columns: min-content min-content auto min-content;
  grid-gap: 32px;
  gap: 32px;
  justify-items: center;
  align-items: start;

  /* padding: 39px 80px; */
  margin: 0 auto;
}

.header__pic {
  padding: 0;
  margin: 0;
  border-radius: 50%;
  width: 187px;
  aspect-ratio: 1;
  object-fit: contain;
  object-position: center;
}

.header__info-wrapper {
  justify-self: start;
  align-self: center;
}

.header__subtitle_profile {
  font-size: 32px;
}

@media screen and (max-width: 768px) {
  .header {
    padding: 20px 32px 64px 32px;
  }

  .header__title {
    font-size: 36px;
  }

  .header__subtitle {
    font-size: 16px;
    max-width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .header__wrapper {
    grid-template-columns: 1fr 1fr;
    padding-bottom: 40px;
  }

  .header__button_place_left {
    grid-column: 1/2;
    grid-row: 1/2;
    justify-self: start;
  }
  .header__button_place_right {
    grid-column: 2/3;
    grid-row: 1/2;
    justify-self: right;
  }

  .header__info-wrapper {
    grid-column: 1/3;
    grid-row: 2/3;

    align-self: center;
    justify-self: center;

    align-items: center;
  }

  .header__info-wrapper > * {
    text-align: center;
  }

  .header__pic {
    grid-column: 1/3;
    grid-row: 3/4;
  }
}

@media screen and (max-width: 600px) {
  .header__button-img {
    display: block;
  }

  .header__button-text {
    display: none;
  }

  .header__button {
    border: none;
  }
}

.users {
  max-width: 1440px;
  margin: 0 auto;
  padding: 48px 80px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 60px;
  gap: 60px;
}

.users__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(305px, -webkit-min-content));
  grid-template-columns: repeat(auto-fit, minmax(305px, min-content));
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;

  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
  list-style: none;
  width: 100%;
}

.users__card {
  padding: 36px 20px 20px 20px;
  margin: 0;
  box-sizing: 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  grid-gap: 16px;
  gap: 16px;

  cursor: pointer;

  box-shadow: 0px 1px 4px rgba(51, 51, 51, 0.15);
  border-radius: 10px;

  transition: all 300ms ease-in;
}

.users__card:hover {
  box-shadow: 0px 2px 8px rgba(27, 27, 27, 0.15);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.users__pic {
  border-radius: 50%;
  max-width: 124px;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: center;
}

.user__name {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 20px;
  color: black;
}

.user__like {
  align-self: flex-end;

  width: 30px;
  height: 28px;
  background-color: #f8f8f8;
  border-radius: 4px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;

  cursor: pointer;

  transition: all 300ms ease-in;
}

.user__like:hover {
  opacity: 0.7;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.user__like_active {
  background-image: url(data:image/png;base64,);
}

.users__more {
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;

  font-size: 16px;

  background: transparent;
  border: 1px solid #151317;
  border-radius: 8px;
  cursor: pointer;

  transition: all 300ms ease-in;
}

.users__more:hover {
  opacity: 0.7;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.users__more-pic {
  transition: all 300ms ease-in;
}

.users__more:hover .users__more-pic {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.users__more:disabled {
  cursor: initial;
  border: 1px solid rgb(180, 180, 180);
}

.users__more:disabled .users__more-pic{
  display: none;
}

.users__more:disabled:hover{
  -webkit-transform: unset;
          transform: unset;
  opacity: 1;
}


.profile {
  max-width: 1440px;
  margin: 0 auto;
  padding: 50px 0 50px 198px;
  box-sizing: border-box;

  display: flex;
  grid-gap: 140px;
  gap: 140px;
}

.profile__paragraph-wrapper {
  flex-basis: 60%;

  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}

.profile__paragraph {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 16px;
  color: #151317;
}

.profile__aside {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;

  flex-grow: 1;

  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}

.profile__item{
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-size: 16px;
  color: #151317;
}

@media screen and (max-width:1024px) {
  .profile{
    padding: 50px 80px;
    grid-gap: 60px;
    gap: 60px;
  }
}

@media screen and (max-width:768px) {
  .profile{
    flex-direction: column-reverse;
    grid-gap: 34px;
    gap: 34px;
    padding: 16px 32px;
  }
}


.login {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  display: flex;
  align-items: center;
  justify-content: center;
}

.login__form {
  min-width: 500px;
  padding: 16px;
  margin: 0;
  box-sizing: border-box;

  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
  border-radius: 16px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-gap: 5px;
  gap: 5px;
}

.login__header {
  padding: 0 0 15px 0;
  margin: 0;
  box-sizing: border-box;

  font-size: 20px;
  color: #151317;
}

.login__label {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-size: 16px;

  color: #151317;

  padding-bottom: 10px;
}

.login__input {
  padding: 12px 8px 12px 16px;
  margin: 0;
  box-sizing: border-box;
  background: #f8f8f8;
  border-radius: 8px;
  border: 0;
  width: 100%;
}

.login__input:focus{
  outline-offset: 4px;
}

.login__input_error {
  border: 1px solid #ff6161;
}

.login__error {
  padding: 2px 0 10px 0;
  margin: 0;
  box-sizing: border-box;

  font-size: 10px;
  line-height: 12px;
  color: #ff6161;

  display: inline-block;
  height: 18px;
}

.login__pasword-wrapper {
  width: 100%;
  position: relative;
}

.login__psw-img {
  width: 24px;
  aspect-ratio: 1;
  position: absolute;
  right: 16px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
}

.login__button {
  width: 100%;

  padding: 8px 12px;
  margin: 0;

  background: #512689;
  border-radius: 8px;
  border: 0;

  color: white;
  cursor: pointer;
}

.login__button:disabled{
  cursor: unset;
  background:#7a7286;
  opacity: .3;
}

@media screen and (max-width: 610px) {
  .login__form{
    width: 100%;
    min-width: unset;
  }
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

